.header {
  padding: 27px 0;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 991px) {
  .header__nav {
    display: none;
  }
}

.header__list {
  display: flex;
  gap: 30px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.header__item {
  position: relative;
}

.header__item::after {
  content: '';
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: 0%;
  height: 1px;
  background-color: #333333;
  transition: .3s width ease;
}

.header__item:hover::after {
  width: 100%;
}

.header__link {
  font-size: calc((0.2 / (120 - 20) * ((100vw - 20rem)) + 0.8rem));
  text-decoration: none;
  color: #333333;
  line-height: 140%;
}

@media (max-width: 575px) {
  .header__search-form {
    display: none;
  }
}

.header__search-btn {
  background-color: transparent;
  border: none;
  margin-right: 20px;
  cursor: pointer;
}

@media (max-width: 1199px) {
  .header__search-btn {
    margin-right: 10px;
  }
}

.header__search-btn:hover .header__search-img {
  transition: .3s transform ease;
  transform: scale(1.1);
}

.header__search-input {
  border: none;
  border-bottom: 1px solid rgba(51, 51, 51, 0.2);
  outline: none;
  font-size: 1rem;
  max-width: 150px;
}

@media (max-width: 1199px) {
  .header__search-input {
    max-width: 100px;
  }
}

@media (max-width: 991px) {
  .header__search-input {
    max-width: 150px;
  }
}

.header__search-img {
  width: 25px;
  height: 24px;
}

.header__search-img use {
  fill: #3D348B;
}

.header__language {
  position: relative;
  padding: 5px;
}

.header__language::after {
  content: url("./assets/icons/arrow-down.svg");
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  z-index: -1;
}

.header__select {
  font-family: "PT Sans", sans-serif;
  position: relative;
  border: none;
  font-weight: 900;
  font-size: 1rem;
  appearance: none;
  width: 40px;
  background-color: transparent;
  padding: 5px;
  cursor: pointer;
}

.header__logo-svg {
  width: 108px;
  height: 30px;
}

.header__logo-svg use {
  fill: #3D348B;
}

.header-burger__btn {
  display: none;
  width: 50px;
  height: 50px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  background-color: #3D348B;
  border-radius: 100%;
  transition: .3s background-color ease;
  border: none;
  cursor: pointer;
}

@media (max-width: 991px) {
  .header-burger__btn {
    display: flex;
  }
}

.header-burger__btn_active .header-burger__line {
  position: relative;
  transition: .4s transform ease;
}

.header-burger__btn_active .header-burger__line:nth-child(1) {
  transform: rotate(45deg);
  top: 4px;
}

.header-burger__btn_active .header-burger__line:nth-child(2) {
  display: none;
}

.header-burger__btn_active .header-burger__line:nth-child(3) {
  transform: rotate(-45deg);
  top: -4px;
}

.header-burger__btn:hover {
  background-color: #6b61c4;
}

.header-burger__line {
  display: block;
  width: 20px;
  height: 3px;
  background-color: #FFFFFF;
}

.header-burger__menu {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  position: fixed;
  top: 0;
  left: -100%;
  bottom: 0;
  transform: translateX(-50%);
  padding: 50px 20px;
  background-color: #3D348B;
  transition: .3s opacity ease, .3s left ease;
  z-index: 1000;
}

.header-burger__menu_active {
  left: 50%;
  opacity: 1;
  overflow: scroll;
  visibility: visible;
}

@media (max-width: 767px) {
  .header-burger__menu {
    padding: 70px 20px;
  }
}

.header-burger__close {
  position: absolute;
  top: 20px;
  right: 50px;
  font-size: 2rem;
  color: #FFFFFF;
  transition: .3s transform ease;
  font-weight: 400;
  border: none;
  background-color: transparent;
}

.header-burger__close:hover {
  transform: rotate(90deg);
}

.header-burger__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  font-size: 1.2rem;
  font-weight: 400;
  list-style: none;
  padding: 0;
  margin: 0;
}

.header-burger__link {
  color: #FFFFFF;
  text-decoration: none;
}

