.calculator__choose-header {
  display: block;
  margin-bottom: 20px;
}

.calculator__input {
  font-size: 0.8125rem;
  font-weight: 400;
  text-transform: uppercase;
  min-height: 35px;
  padding: 0 0 0 20px;
  border-radius: 5px;
  border: 1px solid rgba(51, 51, 51, 0.2);
}

.calculator__select-wrapper {
  position: relative;
}

.calculator__select-wrapper::after {
  content: '';
  display: block;
  position: absolute;
  top: 55%;
  right: 10px;
  transform: translateY(-50%);
  border: 5px solid transparent;
  border-top: 7px solid #333333;
  z-index: -1;
}

.calculator__select {
  width: 100%;
  min-height: 35px;
  padding: 0 0 0 20px;
  border-radius: 5px;
  border: 1px solid rgba(51, 51, 51, 0.2);
  appearance: none;
  background-color: transparent;
  z-index: 10;
}

.calculator__textarea {
  border-radius: 5px;
  border: 1px solid rgba(51, 51, 51, 0.2);
  padding: 20px;
  resize: unset;
}

.calculator__choose-header {
  font-size: 0.8125rem;
  font-weight: 900;
  text-transform: uppercase;
}

.calculator__input-file {
  font-family: "PT Sans", sans-serif;
  display: none;
}

.calculator__label-file {
  margin-right: 20px;
}

@media (max-width: 767px) {
  .calculator__label-file {
    margin-right: 3px;
  }
}

.calculator__file-choosed {
  font-size: 0.8125rem;
  font-weight: 900;
  text-transform: uppercase;
}

.calculator__inform {
  display: flex;
  flex-direction: column;
  gap: 20px;
  grid-column-start: 1;
  grid-column-end: 3;
  padding: 60px 0;
}

@media (max-width: 575px) {
  .calculator__inform {
    grid-column-end: 2;
  }
}

.calculator__inform-label {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 0.8125rem;
  font-weight: 900;
  text-transform: uppercase;
}

.calculator__inform-title {
  padding-bottom: 40px;
}

.calculator__inform-checkbox {
  position: relative;
  appearance: none;
}

.calculator__inform-checkbox::after {
  content: '';
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid #333333;
}

.calculator__inform-checkbox:checked::before {
  content: url("./assets/icons/success.svg");
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.calculator__feedback-title {
  padding-bottom: 60px;
  grid-column-start: 1;
  grid-column-end: 3;
}

@media (max-width: 575px) {
  .calculator__feedback-title {
    grid-column-end: 2;
  }
}

.calculator__submit {
  margin: 0;
  padding: 0;
  grid-column-start: 1;
  grid-column-end: 3;
  justify-content: center;
  margin-top: 60px;
}

@media (max-width: 575px) {
  .calculator__submit {
    grid-column-end: 2;
  }
}

.calculator-title {
  font-size: calc((0.4875 / (120 - 20) * ((100vw - 20rem)) + 1.2rem));
  font-weight: 900;
  margin: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
  min-width: 320px;
  margin: 0;
  padding: 0;
  line-height: 140%;
  color: #333333;
}

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

.container {
  max-width: 1326px;
  margin: 0 auto;
  padding: 0 15px;
}

.section-title {
  font-size: calc((0.8 / (120 - 20) * ((100vw - 20rem)) + 1.7rem));
  font-weight: 900;
  color: #333333;
  margin: 0;
  line-height: 140%;
}

.btn {
  font-family: "PT Sans", sans-serif;
  font-size: calc((0.125 / (120 - 20) * ((100vw - 20rem)) + 0.875rem));
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 15px;
  border: none;
  text-transform: uppercase;
  background-color: transparent;
  font-weight: 900;
  transition: .1s transform ease;
  cursor: pointer;
}

.btn:hover {
  transform: scale(1.05);
}

.btn_main {
  color: #3D348B;
}

.btn_sec {
  color: #FFFFFF;
}

.btn-arrow {
  position: relative;
}

.btn-arrow::after {
  content: '';
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: transparent;
  border: 2px solid #FFFFFF;
}

.btn-arrow::before {
  content: url("./assets/icons/arrow-right.svg");
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
}

.btn-arrow svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-arrow_white::after {
  border: 2px solid #FFFFFF;
}

.btn-arrow_white::before {
  content: url("./assets/icons/arrow-right-white.svg");
}

.btn-arrow_purple::after {
  border: 2px solid #3D348B;
}

.btn-arrow_purple::before {
  content: url("./assets/icons/arrow-right-purple.svg");
}

.btn-file {
  background-color: #5DB7DE;
  color: #FFFFFF;
  font-size: 0.8125rem;
  font-weight: 900;
  border-radius: 10px;
  padding: 8px 16px;
  border: none;
  line-height: 140%;
  text-align: center;
  text-transform: uppercase;
  transition: .3s background-color ease-in-out;
}

.btn-file:hover {
  background-color: #33a4d5;
}
